export default{
	props:{
		classes : {
			type : Array,
			default : function(){
				return new Array()
			}
		}
	},
	data() {
		return {classesToUse: ''}
	},
	watch:{
		classes : function(val){this.classesToUse = this.classes.join(' ');}
	},
	created:function(){
		this.classesToUse = this.classes.join(' ');
	}
}
